:root {
    --footerColor: #6d7b5b;
}

footer {
    /* background-color: var(--footerColor); */
    position: relative;
    z-index: -50;

    bottom: 0;

    background-size: cover;
    background-position-x: 80%;
    background-position-y: 50%;
    background-repeat: no-repeat;

    background-size: 150%;

    filter: blur(1.2rem);

    height: 10rem;
    width: 100%;
}

@media screen and (min-width:516px) {
    footer {
        height: 12rem;
    }
    
}

@media screen and (min-width:768px) {
    footer {
        height: 12rem;
        background-size: 130%;
        background-position-x: 80%;
    }
    
}