@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;1,700&display=swap');

:root {
    --boxColor: #467E03;
    --mealFontColor : #6F5436;
    --listFontColor : #213b00;
}

.body {
    height: max-content;
}

main {
    margin: 0 auto ;
    padding: 1.5rem 0;

    width: 90%;
    transform: translate(0, -4rem);

    color: #000000;

    background-color: #FFFFFF;
    /* backdrop-filter: blur(10px); */

    border: 1px solid var(--boxColor);
    box-shadow: 0px 2px 10px var(--boxColor);
    border-radius: 0.6rem;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.menu {
    width: max-content;
    color: var(--mealFontColor);
}

.meal:first-of-type {
    margin-top: 0;
}

.meal {                 /* Portate */
    font-family: 'Poppins', sans-serif;
    margin-top: 1rem;
    
    font-size: 1.4rem;

    list-style-type: none;

    font-weight: 800;
}

.meal>ul>li {           /* Singoli piatti */
    
    font-size: 0.8em;
    font-weight: 500;

    color: var(--listFontColor);

    margin-left: 1.2em;
    margin-bottom: 0.3em;

    list-style-type: none;
}

.meal>ul>li:last-of-type { 
    margin-bottom: 0;
}

/* .meal>ul>li::before {
    content: "";
    margin-right: 0.2rem;
} */

.meal>ul {
    margin: 0.7em auto;
}

@media screen and (min-width:768px) {
    main {
        width: 30rem;
        transform: translate(0, -10rem);
    }
}